<template>
  <div id="app">
    <ScrollTopButton/>
    <router-view v-if="isRouterAlive"></router-view>
  </div>
</template>

<script>
import ScrollTopButton from './components/ScrollTopButton'
import Swal from 'sweetalert2/dist/sweetalert2.js'
import 'sweetalert2/src/sweetalert2.scss'

export default {
  name: 'App',
  components: {
    ScrollTopButton
  },
  provide(){
    return{
      reload: this.reload
    }
  },
  data(){
    return{
      isRouterAlive: true
    }
  },
  methods:{
    reload () {
      console.log('reload occure')
      this.isRouterAlive = false
      // 在 DOM 已掛載和渲染完成後，自動執行 nextTick() 內的程式碼。
      // 如要做迴圈更新資料，可以等渲染完DOM後，再用nextTick()做一次性更新
      // 這樣可以減少資料重複更新與不必要的計算。
      this.$nextTick(() => {
        this.isRouterAlive = true
        window.scroll({
          top: 0,
          left: 0,
          behavior: 'smooth'
        })
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Send Email Success !!!',
          showConfirmButton: false,
          timer: 1500
        })
      }) ;
    },
  }
}
</script>

<style lang="scss">
@import './assets/scss/all';

</style>
